/**
 * @class demo.view.TextComponent
 * @implements EventListener
 *
 * TextComponent is implemented without any abstract concept of a UiComponent
 * or Sprite, and uses standard HTML5 methods to manipulate the DOM. It
 * does however implement the {@link #handleEvent W3C EventListener interface}
 * which means it can be passed directly to HTMLElement#addEventListener, rather
 * than providing a Function, leading to tidier code and no need for Function#bind
 * to preserve the execution scope of its methods.
 */
import puremvc from 'puremvc';
import * as Xhr from '../../common/XmlHttpRequest.js';
import RePhaser from '../../common/RePhaser.js';
import Utils, {ResourcesManager, AudioTagPool} from '../../common/Utils.js';
import commit from '../../common/Version.js';
import PhoneUtils from '../../common/PhoneUtils.js';
import FontUtils from '../../common/FontUtils.js';

puremvc.define
(
    // CLASS INFO
    {
        name: 'diygamePlayer.view.component.Scene',

        /** @constructor */
        constructor: function () {
            //test8
            var scene = this;
            const ww = flashvars.vertical ? 540 : 960;
            const hh = flashvars.vertical ? 960 : 540;
            var state = {
                init: function () {
                    // Delete this init block or replace with your own logic.
                    !scene.game.device.desktop && (scene.game.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL);
                    if(flashvars.wap && flashvars.wap=== "qh"){
                        scene.game.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL;//群黑平台游戏默认全屏显示
                    }
                    scene.game.scale.fullScreenScaleMode = Phaser.ScaleManager.SHOW_ALL;
                    scene.game.scale.pageAlignVertically = true;
                    scene.game.scale.pageAlignHorizontally = true;
                    scene.game.stage.disableVisibilityChange = true;
                    const isIphone = ()=>{
                        return (navigator.userAgent.indexOf('iPhone') > -1 && (window.orientation && window.orientation === 0));
                    }
                    scene.game.scale.setResizeCallback((scaleManager, rectangle) => {
                        const game = document.getElementById("game");
                        let w = isIphone() ? (global.width): (window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth)
                        let h = isIphone() ? (global.height): (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight);
                        if(isIphone()){
                            if(w > h){
                                w = global.height;
                                h = global.width;
                            }
                        }
                        game.style.width = w + 'px';
                        game.style.height = h + 'px';
                        if (w > h) {
                            if(flashvars.vertical){
                                if (scene.game.world.angle !== 90) {
                                    scene.game.scale.setGameSize(960, 540);
                                    scene.game.world.angle = 90;
                                    scene.game.world.setBounds(-960, 0, 540, 960);
                                    scene.game.scale.refresh();
                                }
                            }else{
                                if (scene.game.world.angle !== 0) {
                                    scene.game.scale.setGameSize(960, 540);
                                    scene.game.world.angle = 0;
                                    scene.game.world.setBounds(0, 0, 960, 540);
                                    scene.game.scale.refresh();
                                }
                            }
                        } else {
                            if(flashvars.vertical){
                                if (scene.game.world.angle !== 0) {
                                    scene.game.scale.setGameSize(540, 960);
                                    scene.game.world.angle = 0;
                                    scene.game.world.setBounds(0, 0, 540, 960);
                                    scene.game.scale.refresh();
                                }
                            }else{
                                if (scene.game.world.angle !== 90) {
                                    scene.game.scale.setGameSize(540, 960);
                                    scene.game.world.angle = 90;
                                    scene.game.world.setBounds(-960, 0, 960, 540);
                                    scene.game.scale.refresh();
                                }
                            }
                        }

                    });
                    scene.game.load.loadImageTag = RePhaser.loadImageTag;
                    scene.game.load.onSoundExtendLoad = new Phaser.Signal();
                    scene.game.load.onSoundExtendLoadError = new Phaser.Signal();
                    PhoneUtils.load = scene.game.load;
                    scene.game.renderer.renderSession.roundPixels = true;
                    scene.game.sound.decode = RePhaser.soundDecode;
                    scene.game.sound.onSoundDecodeError = new Phaser.Signal();

                    //fix iOS audio tag volume always stays set at 1

                    const init = () => {
                        scene.game.sound.touchLocked = false;
                        AudioTagPool.fill();
                        const keys = scene.game.cache.getKeys(Phaser.Cache.SOUND);
                        keys.forEach(key => {
                            var sound = scene.game.cache.getSound(key);
                            if (sound && !AudioTagPool.exist(sound.data)) {
                                scene.game.cache.reloadSound(key);
                            }
                        });
                    };

                    if (scene.game.device.iOS) {
                        if (scene.game.device.iOSVersion > 8) {
                            scene.game.input.touch.addTouchLockCallback(init, scene.game, true);
                        } else {
                            scene.game.input.touch.addTouchLockCallback(init, scene.game);
                        }
                    } else {
                        init();
                    }

                    ResourcesManager.cache = scene.game.cache;
                    ResourcesManager.load = scene.game.load;
                    scene.game.destroyComponent.add(key => {
                        ResourcesManager.subRefCount('img', key, 'play');
                    });
                    window.ResourcesManager = ResourcesManager;
                },
                preload: function () {
                },
                create: function () {
                    this.game.canvas.oncontextmenu = e => e.preventDefault();
                    // State create logic goes here
                    // Create simple text display for current Phaser version
                    let startBk = scene.game.add.graphics(0, 0);
                    startBk.name = 'startBk';
                    startBk.beginFill(0x62d3d5);
                    startBk.drawRect(0, 0, ww, hh);
                    startBk.endFill();
                    //scene.game.stage.backgroundColor = "#62d3d5";
                    if (scene.onRendered) {
                        scene.onRendered();
                    }
                },
                update: function () {
                    // State Update Logic goes here.
                }
            };
            // 闪退手机浏览器版本: Chrome/50.0.2661.86 & Chrome/51.0.2704.81 渲染方式改为自动
            let phaserRender = Phaser.CANVAS;
            let agentV = navigator.userAgent.match(/chrome\/((([0-9]*)+(\.))*)+([0-9]*)/gi);
            if (agentV) {
                if (agentV[0] == 'Chrome/50.0.2661.86' || agentV[0] == 'Chrome/51.0.2704.81') {
                    if (-1 === navigator.userAgent.indexOf('Android 6.0.1; MI 4LTE')
                        && -1 === navigator.userAgent.indexOf('vivo Y67')) {
                        phaserRender = Phaser.AUTO;
                    }
                }
                else if (agentV[0] == 'Chrome/46.0.2490.76' && -1 !== navigator.userAgent.indexOf('OPPO R9s')) {
                    phaserRender = Phaser.AUTO;
                }
            }
            scene.game = new Phaser.Game(
                ww,
                hh,
                phaserRender,
                'game',
                state
            );
            scene.game.preserveDrawingBuffer = true;
            window.game = scene.game;
            window['AudioContext'] = null;
            window['webkitAudioContext'] = null;
            console.log('versionCommit: ', commit);
        }
    },

    // INSTANCE MEMBERS
    {
        showLogo: function () {
            let logo_preloader = this.game.world.getByName('logo_preloader');
            if (!logo_preloader) {
                logo_preloader = this.game.add.image(this.game.world.width / 2, this.game.world.height / 2, 'logo_preloader');
                logo_preloader.name = 'logo_preloader';
                logo_preloader.anchor.setTo(0.5, 0.5);
            }
            logo_preloader.visible = true;
        },

        //离线背景图片
        showOfflineBg:function () {
            let offlinebg = this.game.world.getByName('offlinebg');
            if (!offlinebg) {
                const opg = Utils.localStorageGetItem('shanyi3000opg');
                offlinebg = this.game.add.image(this.game.world.width / 2, this.game.world.height / 2, opg ? 'offlinebg' : 'Operation_tip');
                offlinebg.name = 'offlinebg';
                offlinebg.inputEnabled = true;
                offlinebg.anchor.setTo(0.5, 0.5);
                this.game.world.setChildIndex(offlinebg,1);//放底部
            }
            offlinebg.visible = true;
        },

        showLoading: function () {
            let logo_preloader = this.game.world.getByName('logo_preloader');
            let loading_gif = this.game.world.getByName('loading_gif');
            if (!loading_gif) {
                loading_gif = this.game.add.image(this.game.world.width / 2, flashvars.vertical ? (this.game.world.height / 2 - 100) : this.game.world.height, 'loading_gif');
                loading_gif.anchor.set(0.5,0.5);
                loading_gif.x = this.game.world.width / 2;
                loading_gif.y = flashvars.vertical ? (this.game.world.height / 2 - loading_gif.height / 2) : (this.game.world.height / 2 - 80);
                loading_gif.name = 'loading_gif';
                this.addGifAnimation(loading_gif);
            }
            if (logo_preloader) {
                logo_preloader.scale.setTo(0.35, 0.35);
                logo_preloader.x = this.game.world.width - logo_preloader.width + 25;
                logo_preloader.y = this.game.world.height - logo_preloader.height + 12;
            }
            loading_gif.visible = true;
            return true;
        },

        destroyLogo: function () {
            const startBk = this.game.world.getByName('startBk');
            const logo_preloader = this.game.world.getByName('logo_preloader');
            const loading_gif = this.game.world.getByName('loading_gif');
            const logInfo = this.game.world.getByName('logInfo');
            //const offlinebg = this.game.world.getByName("offlinebg");
            startBk && startBk.destroy();
            logo_preloader && logo_preloader.destroy();
            loading_gif && loading_gif.destroy();
            logInfo && logInfo.destroy();
            //offlinebg && offlinebg.destroy();
        },

        preLoadCertainAssets: function () {
            if(!flashvars.vertical)this.game.load.spritesheet('wind', './img/wind.png', 960, 540);
            this.game.load.image('icon_back', './img/icon_back.png');
            this.game.load.image('icon_back_over_pressed', './img/icon_back_over_pressed.png');
            this.game.load.image('icon_candy', './img/icon_candy.png');
            this.game.load.image('icon_candy_over_pressed', './img/icon_candy_over_pressed.png');
            this.game.load.image('icon_get', './img/icon_get.png');
            this.game.load.image('icon_get_hover_pressed', './img/icon_get_hover_pressed.png');
            this.game.load.image('icon_save', './img/icon_save.png');
            this.game.load.image('icon_save_over_pressed', './img/icon_save_over_pressed.png');
            //this.game.load.image('logo_preloader', './img/logo_preloader.png');
            //this.game.load.image('loading_gif', './img/loading_gif.gif');
            this.game.load.image('cloud', './img/cloud.png');
            this.game.load.image('cloud_hover', './img/cloud_hover.png');
            this.game.load.image('local', './img/local.png');
            this.game.load.image('local_hover', './img/local_hover.png');
            this.game.load.image('icon_success', './img/icon_success.png');
            this.game.load.image('icon_error', './img/icon_error.png');
            this.game.load.image('light', './img/light.png');
            this.game.load.image('toast_loading', './img/toast_loading.gif');
            this.game.load.image('app_button_speed', './img/app_button_speed.png');
            this.game.load.image('app_icon_speed', './img/app_icon_speed.png');
            if(flashvars.client === 'wap') this.game.load.image('wapback', './img/wapback.png');//wap端增加退出按钮
            this.game.load.image('Operation_tip', flashvars.vertical ? "./img/Operation_tip_1.gif" : './img/Operation_tip.png');
            this.game.load.image('Cloud_record_tip', flashvars.vertical ? "./img/Cloud_record_tip_1.png" : './img/Cloud_record_tip.png');
            this.game.load.image('offlinebg','./img/offlinebg.png');//离线背景图片
            this.game.load.image('Continue', './img/Continue.png');
            this.game.load.image('menuMore', './img/more.png');
            this.game.load.image('menuMoreHover', './img/more_hover.png');

            if (flashvars.offline === '1') {
                if(flashvars.vertical){
                    for (let i=0; i<20; i++) {
                        this.game.load.image('/weather/wind/feng_' + Utils.formatNum(i) + '.png', './img/wind/feng_' + Utils.formatNum(i) + '.png');
                    }
                }
                for (let i=0; i<50; i++) {
                    this.game.load.image('/weather/rain/xiayu_' + Utils.formatNum(i) + '.png', './img/rain/xiayu_' + Utils.formatNum(i) + '.png');
                }
                for (let i=0; i<120; i++) {
                    this.game.load.image('/weather/snow/xuehua_' + Utils.formatNum(i) + '.png', './img/snow/xuehua_' + Utils.formatNum(i) + '.png');
                }
                for (let i=0; i<90; i++) {
                    this.game.load.image('/weather/mapleLeaves/fengye_' + Utils.formatNum(i) + '.png', './img/mapleLeaves/fengye_' + Utils.formatNum(i) + '.png');
                }
                for (let i=0; i<90; i++) {
                    this.game.load.image('/weather/beachBlossom/taohua_' + Utils.formatNum(i) + '.png', './img/beachBlossom/taohua_' + Utils.formatNum(i) + '.png');
                }
                for (let i=0; i<90; i++) {
                    this.game.load.image('/weather/blossom/yinghua_' + Utils.formatNum(i) + '.png', './img/blossom/yinghua_' + Utils.formatNum(i) + '.png');
                }
                for (let i=0; i<50; i++) {
                    this.game.load.image('/weather/lightning/shandian_' + Utils.formatNum(i) + '.png', './img/lightning/shandian_' + Utils.formatNum(i) + '.png');
                }
            }
        },

        preLoad: function () {
            return new Promise((resolve, reject) => {
                this.preLoadCertainAssets();
                this.game.load.onLoadStart.addOnce(() => {
                    this.addLog('正在准备播放..')
                });
                this.game.load.onFileComplete.add((progress, cacheKey, success, totalLoaded, totalFiles) => {
                    //if ('logo_preloader' === cacheKey) this.showLogo();
                    if("offlinebg" === cacheKey) this.showOfflineBg();
                    this.addLog('正在准备播放.. ' + progress + "%", true);
                });
                this.game.load.onLoadComplete.addOnce(() => {
                    this.game.load.onFileComplete.removeAll();
                    this.addLog("正在准备播放..【成功】", true);
                    return resolve();
                });

                this.game.load.start();
            })
        },

        getGameJson: function (iswapother) {
            flashvars.uuid = flashvars.uuid || '41138941-7f1c-4701-9a94-394893483662';
            const uuid = flashvars.uuid;

            document.title = flashvars.name && decodeURI(flashvars.name) || "测试";

            const onProgress = evt => {
                if (evt.lengthComputable) {
                    const percentComplete = evt.loaded / evt.total;
                    this.addLog("加载剧情配置.. " + ~~percentComplete + '%', true);
                } else {
                    this.addLog("加载剧情配置.. " + evt.loaded, true);
                }
            };

            this.addLog("加载用户配置..");
            const getGame = iswapother ? Xhr.getOtherGame : (flashvars.type == 1 ? Xhr.getTestGame : Xhr.getGame);
            return getGame(uuid, onProgress).then(game => {
                this.addLog("加载剧情配置..【成功】", true);
                return game;
            }, e => {
                this.addLog("发生错误：" + e);
                return Promise.reject(e);
            });
        },

        addLog: function (info, replace) {
            if (!info) {
                return;
            }
            let logInfo = this.game.world.getByName('logInfo');
            if (!logInfo) {
                const style = {font: "16px 宋体", fill: "#fff"};
                //logInfo = this.game.add.text(18, 540 - 36, '', style);
                logInfo = this.game.add.text(18, this.game.world.height - 36, '', style);
                logInfo.name = 'logInfo';
            }
            let text = logInfo.text;
            const textGroup = text.split(/\r?\n/gi).filter(t => !!t);
            if (replace) {
                textGroup[textGroup.length - 1] = info;
            } else {
                textGroup.push(info);
            }
            logInfo.text = '';
            textGroup.slice(-4).map(str => logInfo.text += str + '\n');
            //logInfo.y = 540 - textGroup.length * 26;
            logInfo.y = this.game.world.height - textGroup.length * 26;
        },

        addButton: function (x, y, width, height, key_normal, key_active, onClick, text, audio, name, parent, isCheckBox) {
            const changeImageText = (textInfo, content, width, height, textObj) => {
                const txtImgOld = textObj.children[0];
                const strContent = content ? content : txtImgOld ? txtImgOld.text : '';

                if (!strContent) {
                    return;
                }

                const fontSize = textInfo.fontSize ? parseInt(textInfo.fontSize) : 25;
                const color = textInfo.color ? textInfo.color : '#fefefe';
                const fh = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontHeight(fontSize) : fontSize;
                const fw = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontWidth(fontSize) : fontSize;
                if (color.indexOf('rgb') !== -1) {
                    const rgb = color.replace('rgb(', '').replace(')', '').split(',');
                    textObj.addColor('#' + ('00000' + (parseInt(rgb[0]) << 16 | parseInt(rgb[1]) << 8 | parseInt(rgb[2])).toString(16)).substr(-6), 0);
                }
                else {
                    textObj.addColor(color === '#ffffff' ? '#fefefe' : color, 0);
                }
                textObj.wordWrapWidth = 540/*width*/;
                this.sceneProxy.addImageFontText([{data:strContent}], fw, fh, textObj, this.sceneProxy.context);
                const w = textObj.children[0].extWidth ? textObj.children[0].extWidth : fw * strContent.length;
                const x = (width - w) / 2 - (textObj.children[0].extMargin ? textObj.children[0].extMargin : 0);
                const y = (height - fh) / 2;
                textObj.children[0].x = x;
                textObj.children[0].y = y;
                textObj.children[0].text = strContent;

                if (txtImgOld) {
                    txtImgOld.children.forEach(item => item.fontImg.destroy());
                    txtImgOld.destroy();
                }

                textObj.text = ' ';
            };
            const changeBackground = (buttonGroup, key, text, clear) => {
                const background = buttonGroup.getByName('background');
                const btnObj = buttonGroup.getByName('button');
                const txtObj = buttonGroup.getByName('text');
                const bk_key = this.game.load.cache.checkImageKey(key) ? key : null;

                if (!btnObj) {
                    return
                }

                if (txtObj && text) {
                    if (FontUtils.fontFamily) {
                        changeImageText(text, '', width, height, txtObj);
                    }
                    else {
                        txtObj.font = text.fontFamily　|| txtObj.font;
                        txtObj.fontSize = text.fontSize ||　txtObj.fontSize;
                        txtObj.fontWeight = text.fontWeight || txtObj.fontWeight;
                        txtObj.fill = text.color ||　txtObj.fill;
                    }

                    txtObj.visible = !text.hidden;
                }
                if (bk_key) {
                    let scale = 1;
                    const {width, height} = btnObj;
                    const image = this.game.cache.getImage(bk_key);

                    if (!image.delay) {
                        scale = Math.min(width / image.width, height / image.height);
                        background.loadTexture(bk_key);
                        background.scale.setTo(scale, scale);
                        background.x = (width - background.width) / 2;
                        background.y = (height - background.height) / 2;
                    }
                    else if (background.key !== bk_key) {
                        this.addGifAnimation(background, bk_key);
                    }
                } else if (!clear) {
                    background.loadTexture(null);
                }
            };

            const btnGroup= this.game.add.group();
            btnGroup.name = name;
            parent && parent.add(btnGroup);
            btnGroup.x = x;
            btnGroup.y = y;

            const bk_key_normal = this.game.load.cache.checkImageKey(key_normal) ? key_normal : null;
            const bk_key_active = this.game.load.cache.checkImageKey(key_active) ? key_active : null;
            const bk = this.game.add.image(0, 0, bk_key_normal, null, btnGroup);
            bk.name = 'background';
            const scale = Math.min(width / bk.width, height / bk.height);
            bk.scale.setTo(scale, scale);
            bk.x = (width - bk.width) / 2;
            bk.y = (height - bk.height) / 2;
            this.addGifAnimation(bk, bk_key_normal);
            let isdown = false;
            let btnobj = null;
            let btnpointer = null;
            const butclick = (obj, pointer)=>{
                btnobj = obj;
                btnpointer = pointer;
            }
            const btn = this.game.add.button(0, 0, null, isCheckBox ? onClick : butclick, this, 0, 0, 0, 0, btnGroup);
            btn.width = width;
            btn.height = height;
            btn.name = "button";
            if (audio) {
                audio.prevolume = audio.prevolume !== undefined ? audio.prevolume : audio.volume;
                this.game.device.desktop ? btn.setOverSound(audio) : btn.setDownSound(audio);
                this.game.device.desktop ? btn.onInputOut.add(() => audio.stop()) : btn.onInputUp.add(() => {});
            }

            let textObj = null;
            if (text) {
                const fontStyle = {};
                let ff = '';
                ff += text.normal.fontWeight ? text.normal.fontWeight + ' ' : 'normal ';
                ff += text.normal.fontSize ? parseInt(text.normal.fontSize) + 'px ' : '';
                ff += text.normal.fontFamily ? text.normal.fontFamily : this.sceneProxy.json_data.font ? this.sceneProxy.json_data.font.fontFamily : '';

                fontStyle.font = ff;
                fontStyle.fill = text.normal.color;
                fontStyle.stroke = text.normal.stroke;
                fontStyle.strokeThickness = text.normal.stroke ? 2 : 0;

                fontStyle.boundsAlignH = "center";
                fontStyle.boundsAlignV = "middle";
                textObj = this.game.add.text(0, 0, text.text, fontStyle, btnGroup);
                textObj.name = 'text';
                if (FontUtils.fontFamily) {
                    changeImageText(text.normal, text.text, width, height, textObj);
                }
                else {
                    textObj.setTextBounds(0, (textObj.height - parseInt(text.normal.fontSize || 12)) / 2, width, height);
                }
                text.normal.hidden && (textObj.visible = false);
            }
            btn.input.mouseOut = function () {
                if (this.sprite === null || !this.sprite.inputEnabled)
                {
                    //  Abort. We've been destroyed.
                    return;
                }
                var data = this._pointerData[0];

                data.isOver = false;
                data.isOut = true;
                data.timeOut = this.game.time.time;

                if (this.useHandCursor && data.isDragged === false)
                {
                    this.game.canvas.style.cursor = "default";
                    this._setHandCursor = false;
                }

                if (this.sprite && this.sprite.events)
                {
                    this.sprite.events.onInputOut$dispatch(this.sprite, null);

                    if (this.sprite && this.sprite.parent && this.sprite.parent.type === Phaser.GROUP)
                    {
                        this.sprite.parent.onChildInputOut.dispatch(this.sprite, null);
                    }
                }
            };
            !isCheckBox && btn.onInputOver.add(() => {
                btn.onOverSound && (btn.onOverSound.volume = (btn.onOverSound.prevolume !== undefined ? btn.onOverSound.prevolume : btn.onOverSound.volume) * Utils.soundVolume)
                changeBackground(btnGroup, bk_key_active, text && text.normal);
                /*this.game.input.mouse.onMouseOut = () => {
                    btn.input.mouseOut();
                };*/
            });
            !isCheckBox && btn.onInputOut.add(() => {isdown = false;changeBackground(btnGroup, bk_key_normal, text && text.normal)});
            !isCheckBox && btn.onInputDown.add(() => {
                isdown = true;
                btn.onDownSound && (btn.onDownSound.volume = (btn.onDownSound.prevolume !== null ?  btn.onDownSound.prevolume: btn.onDownSound.volume) * Utils.soundVolume)
                changeBackground(btnGroup, bk_key_active, text && text.active)
            });
            !isCheckBox && btn.onInputUp.add(() => {isdown = false;changeBackground(btnGroup, bk_key_normal, text && text.normal);onClick && onClick(btnobj,btnpointer)});

            btnGroup.changeBackground = function (key, text, type) {
                changeBackground(this, key, text, type)
            };
            btnGroup.button = btn;
            btnGroup.text = textObj;
            return btnGroup;
        },

        addGifAnimation: function(obj, key) {
            const image = this.game.cache.getImage(obj.key, true);
            const {frames, loopCount} = image.data;
            if (frames) {
                let nCount = 1;
                const setFrame = index => {
                    if (!frames[index] || !frames[index].blobUrl) {
                        return;
                    }
                    const img = new Image();
                    img.onload = e => {
                        const base = new PIXI.BaseTexture(e.target);
                        const texture = new PIXI.Texture(base);
                        obj && obj.parent && obj.setTexture(texture);
                    };
                    //img.src = frames[index].base64;
                    img.src = frames[index].blobUrl;
                };

                const update = index => {
                    Utils.timeout(frames[index].delay * 10).then(() => {
                        setFrame(index);
                        index++;
                        if (index >= frames.length) {
                            if (loopCount && nCount >= loopCount || !obj || !obj.parent) {
                                return;
                            } else {
                                nCount++;
                                update(0)
                            }
                        } else {
                            update(index)
                        }
                    });
                };
                update(0);
            }
        },

        addFitImageSprint: function (x, y, width, height, key, parent, index) {
            const sKey = this.game.load.cache.checkImageKey(key) ? key : null;
            const sprite = this.game.add.sprite(x, y, sKey);
            if (sKey) {
                const scale = Math.min(width / sprite.width, height / sprite.height);
                sprite.pivot.x = sprite.width / 2;
                sprite.pivot.y = sprite.height / 2;
                sprite.scale.setTo(scale, scale);
                sprite.x += sprite.width / 2;
                sprite.y += sprite.height / 2;
            }
            if (index > 0) {
                let i = 0;
                for (i=0; i<parent.children.length; i++) {
                    let rs = parent.getAt(i);
                    if (-1 === rs) break;
                    if (rs.zIndex === index) {
                        parent.removeChildAt(i);
                        rs.destroy();
                        break;
                    }
                    else if (rs.zIndex > index) {
                        break;
                    }
                }
                let childGroup = this.game.add.group();
                childGroup.add(sprite);
                childGroup.zIndex = index;
                parent.add(childGroup, false, i);
            }
            else {
                parent.add(sprite);
            }

            return sprite;
        },



        game: null,
        onRendered: null,
        sceneProxy: null
    },

    // STATIC MEMBERS
    {
        /**
         * @static
         * @type {string}
         */
        TEXT_CHANGED: 'textChanged'
    }
);
